import {
  Box,
  Flex,
  useColorModeValue,
  VisuallyHidden,
} from "@chakra-ui/react";
import * as React from "react";
import logo from "../../assets/mylogo.png";
import { NavContent } from "./NavContent";

function NavBar() {
  return (
    <Box pos="fixed" w="100%">
      <Box
        as="header"
        height="16"
        bg={useColorModeValue("#031330")}
        position="relative"
        color={"white"}
      >
        <Box
          height="100%"
          maxW="7xl"
          mx="auto"
          ps={{
            base: "6",
            md: "8",
          }}
          pe={{
            base: "5",
            md: "0",
          }}
        >
          <Flex
            as="nav"
            aria-label="Site navigation"
            align="center"
            justify="space-between"
            height="100%"
          >
            <Box as="a" href="#" rel="home">
              {/* Replace this ⬇️ with your company name */}
              <VisuallyHidden>Ravishankar</VisuallyHidden>
              <img
                src={logo}
                alt="Ravishankar"
                style={{ borderRadius: "5px", width: "75px" }}
              />
            </Box>
            <NavContent.Desktop
              display={{
                base: "none",
                md: "flex",
              }}
            />
            <NavContent.Mobile
              display={{
                base: "flex",
                md: "none",
              }}
            />
          </Flex>
        </Box>
      </Box>
    </Box>
  );
}

export default NavBar;
