import { Box, Center, Image } from "@chakra-ui/react";
import React from "react";

const StatisticsCard = ({image, desc}) => {
  return (
    <Box
      maxW="225px"
      textAlign="center"
      bg={"white"}
      border="1px solid #e2eaf0"
      borderRadius={"20px"}
      padding={{ base: 1, sm: 2, md: 3, lg: 4 }}
      lineHeight="1.5"
      color="black"
      fontSize="24px"
      _hover={{
        p: "4",
        rounded: "lg",
        bg: "linear-gradient(90.21deg, rgba(54, 116, 170, 0.5) -5.91%, rgba(74, 47, 189, 0.5) 111.58%)",
        cursor: "pointer",
        color: "white",
        borderRadius: "20px",
      }}
    >
      <Center>
        <Image
          boxSize={{
            base: "75px",
            sm: "100px",
            md: "125px",
            lg: "150px",
          }}
          p={1}
          src={image}
          borderRadius="50%"
        />
      </Center>
      <Box fontSize={{ base: "16px", md: "18px", lg: "20px" }} fontWeight="700">
        {desc}
      </Box>
    </Box>
  );
};

export default StatisticsCard;
