import {
  Box,
  Center,
  Flex,
  Heading,
  SimpleGrid,
  Spacer,
} from "@chakra-ui/react";

export default function Github() {
  const responsive = {
    superLargeDesktop: {
      // the naming can be any, depends on you.
      breakpoint: { max: 4000, min: 3000 },
      items: 5,
    },
    desktop: {
      breakpoint: { max: 3000, min: 1024 },
      items: 3,
    },
    tablet: {
      breakpoint: { max: 1024, min: 464 },
      items: 2,
    },
    mobile: {
      breakpoint: { max: 464, min: 0 },
      items: 1,
    },
  };

  return (
    <Box bg={"rgb(54, 116, 170)"}>
      <Box
        id="github"
        height="100%"
        maxW="7xl"
        mx="auto"
        paddingTop={{base:"25px", sm:"75px"}}
        ps={{
          base: "6",
          md: "8",
        }}
        pe={{
          base: "5",
          md: "0",
        }}
      >
        <Center>
          <Box
            as="button"
            borderRadius="md"
            bg="linear-gradient(90.21deg, #AA367C -5.91%, #4A2FBD 111.58%)"
            color="white"
            py={2}
            px={8}
            mb={6}
            _hover={{
              cursor: "default",
            }}
          >
            <Heading size={{base: "md", sm:"md", md:"lg", lg:"xl"}} textAlign={"center"}>Github</Heading>
          </Box>
        </Center>
        <Center width={"100%"}>
          <GitHubCalendar blockSize={20} username="ravipandeydu" />
        </Center>
        <p class="has-line-data" data-line-start="0" data-line-end="1">
          <a href="https://github.com/ashutosh00710/github-readme-activity-graph">
            <img
              src="https://activity-graph.herokuapp.com/graph?username=ravipandeydu&amp;theme=dracula"
              alt="Ravishankar's github activity graph"
            />
          </a>
        </p>
        <table class="table table-striped table-bordered">
          <thead>
            <tr>
              <th>
                <img
                  src="https://github-readme-stats.vercel.app/api?username=ravipandeydu&amp;show_icons=true&amp;theme=tokyonight"
                  alt="Ravishankar's github stats"
                />
              </th>
              <th>
                <img
                  src="https://github-readme-streak-stats.herokuapp.com/?user=ravipandeydu&amp;theme=tokyonight"
                  alt="Ravishankar GitHub Streak"
                />
              </th>
            </tr>
          </thead>
          <tbody>
            <tr>
              <td>
                <img
                  src="https://github-readme-stats.vercel.app/api/top-langs/?username=ravipandeydu&amp;theme=tokyonight"
                  alt="Top Langs"
                />
              </td>
              <td>
                Well…That’s all ♾️ &lt;br&gt; Have a li’l smile 😄{" "}
                <img
                  src="https://readme-jokes.vercel.app/api?theme=tokyonight"
                  alt="Jokes Card"
                />
              </td>
            </tr>
          </tbody>
        </table>
        {/* <img className="background-image-left" src={colorSharp} alt="Image" /> */}
      </Box>
    </Box>
  );
}
