import { Box } from "@chakra-ui/react";
import React from "react";

const SkillCard = ({ title }) => {
  return (
    <Box
      bg={"white"}
      border="1px solid #e2eaf0"
      borderRadius={"20px"}
      padding={{ base: 1, sm: 2, md: 3, lg: 4 }}
      lineHeight="1.5"
      color="black"
      fontWeight={"600"}
      textAlign="center"
      fontSize={{ base: "14px", sm: "16px", md: "18px", lg: "20px" }}
      _hover={{
        transform: "scale(1.1)",
        p: "4",
        rounded: "lg",
        bg: "linear-gradient(90.21deg, rgba(54, 116, 170, 0.5) -5.91%, rgba(74, 47, 189, 0.5) 111.58%)",
        cursor: "pointer",
        color: "white",
        borderRadius: "20px",
        boxShadow:
          "rgb(85, 91, 255) 0px 0px 0px 3px, rgb(31, 193, 27) 0px 0px 0px 6px, rgb(255, 217, 19) 0px 0px 0px 9px",
      }}
    >
      {title}
    </Box>
  );
};

export default SkillCard;
