import { useState } from "react";
// import { Container, Row, Col, Card } from "react-bootstrap";
import contactImg from "../assets/contactme.png";
import { useForm, ValidationError } from "@formspree/react";
// import 'animate.css';
// import TrackVisibility from "react-on-screen";
// import "react-multi-carousel/lib/styles.css";
// import colorSharp from "../assets/resources/color-sharp.png";
// import { AiOutlineMail, AiFillGithub, AiFillLinkedin } from "react-icons/ai";
// import { IoCallOutline } from "react-icons/io5";
import {
  Box,
  Center,
  Flex,
  Heading,
  SimpleGrid,
  Spacer,
  FormControl,
  FormLabel,
  Input,
  Textarea,
  Button,
} from "@chakra-ui/react";

export const Contact = () => {
  const [state, handleSubmit] = useForm("xqkjpwgw");
  const formInitialDetails = {
    firstName: "",
    lastName: "",
    email: "",
    phone: "",
    message: "",
  };
  const [formDetails, setFormDetails] = useState(formInitialDetails);
  const [buttonText, setButtonText] = useState("Send");
  const [status, setStatus] = useState({});

  const onFormUpdate = (category, value) => {
    setFormDetails({
      ...formDetails,
      [category]: value,
    });
  };

  const handleSubmit1 = async (e) => {
    e.preventDefault();
    setButtonText("Sending...");
    let response = await fetch("http://localhost:5000/contact", {
      method: "POST",
      headers: {
        "Content-Type": "application/json;charset=utf-8",
      },
      body: JSON.stringify(formDetails),
    });
    setButtonText("Send");
    let result = await response.json();
    setFormDetails(formInitialDetails);
    if (result.code == 200) {
      setStatus({ succes: true, message: "Message sent successfully" });
    } else {
      setStatus({
        succes: false,
        message: "Something went wrong, please try again later.",
      });
    }
  };

  // if (state.succeeded) {
  //   return <p>Thanks for joining!</p>;
  // }
  return (
    <Box>
      <Flex>
        <Box>
          <img src={contactImg} alt="Contact Us" />
        </Box>
        {state.succeeded ? (
          <h1>Thanks for Contacting me. I will get back to you soon</h1>
        ) : (
          <Box>
            <FormControl onSubmit={handleSubmit}>
              <FormLabel>Get In Touch</FormLabel>
              <Input id="email" type="email" name="email" placeholder="Email" />
              <ValidationError
                prefix="Email"
                field="email"
                errors={state.errors}
              />
              <Textarea id="message" name="message" placeholder="Message" />
              <ValidationError
                prefix="Message"
                field="message"
                errors={state.errors}
              />
              <Button type="submit" disabled={state.submitting}>
                Submit
              </Button>
            </FormControl>
          </Box>
        )}
      </Flex>
    </Box>
  );
};
