import { Box, Image } from "@chakra-ui/react";
import React from "react";

const TechStackCard = ({image, desc}) => {
  return (
    <Box
      bg={"white"}
      border="1px solid #e2eaf0"
      borderRadius={"20px"}
      padding={{ base: "5px", sm: "10px", md: "15px", lg: "20px" }}
      lineHeight="1.5"
      color="black"
      fontWeight={"700"}
      textAlign="center"
      _hover={{
        transform: "scale(1.1)",
        p: "4",
        rounded: "lg",
        bg: "linear-gradient(90.21deg, rgba(54, 116, 170, 0.5) -5.91%, rgba(74, 47, 189, 0.5) 111.58%)",
        cursor: "pointer",
        color: "white",
        borderRadius: "20px",
        boxShadow:
          "rgb(85, 91, 255) 0px 0px 0px 3px, rgb(31, 193, 27) 0px 0px 0px 6px, rgb(255, 217, 19) 0px 0px 0px 9px",
      }}
      boxShadow="rgba(50, 50, 93, 0.25) 0px 50px 100px -20px, rgba(0, 0, 0, 0.3) 0px 30px 60px -30px, rgba(10, 37, 64, 0.35) 0px -2px 6px 0px inset"
    >
      <Image
        src={image}
        alt="html"
        boxSize={{ base: "60px", sm: "75px", md: "100px", lg: "125px" }}
      />
      <Box display={{ base: "none", md: "contents" }}>{desc}</Box>
    </Box>
  );
};

export default TechStackCard;
