import {
  Box,
  Button,
  Center,
  Flex,
  Heading,
  Image,
  Link,
  Spacer,
  Text,
} from "@chakra-ui/react";
import headerImg from "../assets/myroundpic.png";
import resume from "../assets/Ravishankar-Kumar-Resume.pdf";

export const Banner = () => {
  return (
    <Box bg={"#031330"}>
      <Box
        id="home"
        height="100%"
        maxW="7xl"
        mx="auto"
        pb={"50px"}
        color="white"
        paddingTop={"100px"}
        ps={{
          base: "6",
          md: "8",
        }}
        pe={{
          base: "5",
          md: "0",
        }}
      >
        <Box p={4} display={{ md: "flex" }} textAlign={{base: "center", sm: "center"}}>
          <Box width={{sm: '100%', md: '60%'}}>
            <Box
              as="button"
              borderRadius="md"
              bg="linear-gradient(90.21deg, #AA367C -5.91%, #4A2FBD 111.58%)"
              color="white"
              p={2}
              mb={6}
              _hover={{
                cursor:"default"
              }}
            >
              <Heading as='h2' size={{base: "sm", sm:"md", md:"lg", lg:"lg"}}>Welcome to my Portfolio</Heading>
            </Box>

            <Box mb={6}>
              <Heading as='h2' size='3xl'>{`Hi! I'm <Ravishankar />`}</Heading>
            </Box>
            <Text mb={6}>
              A person with good team skills, interpersonal skills, and good
              team cooperation. A Cheerful and optimistic person who can quickly
              master new knowledge and skills.
            </Text>
            <Button bg='white' variant='solid' color={"#031330"} fontWeight="600">
              <a href={resume} download="Ravishankar Kumar Resume">
                My Resume
              </a>
            </Button>
          </Box>
          <Spacer />
          <Center mt={{base:"20px", sm: "20px"}}>
            <Image
              borderRadius="full"
              boxSize="350px"
              src={headerImg}
              alt="Ravishankar"
            />
          </Center>
        </Box>
      </Box>
    </Box>
  );
};
