import { Box, Center, Heading, ListItem, UnorderedList } from "@chakra-ui/react";
import React from "react";

const AboutMe = () => {
  return (
    <Box bg={"#031330"}>
      <Box
        id="about"
        height="100%"
        maxW="3xl"
        mx="auto"
        bg={"white"}
        pt={{base:"25px", sm:"75px"}}
        pb={"35px"}
        borderRadius="20px"
        ps={{
          base: "6",
          md: "8",
        }}
        pe={{
          base: "5",
          md: "0",
        }}
        boxShadow="#031330 0px 0px 0px 2px inset, rgb(54, 116, 170) 10px -10px 0px -3px, #031330 10px -10px"
      >
        <Box>
          <Center>
            <Box
              as="button"
              borderRadius="md"
              bg="linear-gradient(90.21deg, #AA367C -5.91%, #4A2FBD 111.58%)"
              color="white"
              py={2}
              px={8}
              mb={2}
              _hover={{
                cursor: "default",
              }}
            >
              <Heading size={{base: "md", sm:"md", md:"lg", lg:"xl"}} textAlign={"center"}>About Me</Heading>
            </Box>
          </Center>
          <Box>
            
                <Center>🔭 Intrigued to learn new technologies in Web Development.</Center>
                <Center>🌱 I’m Currently learning MERN stack at MASAI school. </Center>
                <Center>💞️ I like to watch tech. videos and reading books.</Center>
                <Center>💬 Have a look at my</Center>
            
              
          </Box>
        </Box>
      </Box>
    </Box>
  );
};

export default AboutMe;
